.gameBody{
    height: 600px;
    width: 600px;
    margin: 0 auto;
    background: #FFF;
    border: 15px solid #bbada0;
    border-radius: 10px;
    position: relative;
    font-size: 50px;
    font-weight: bold;
}
.gameBody .gameCell{
    float: left;
    height: 130px;
    line-height: 130px;
    width: 130px;
    border: 10px solid #bbada0;
    border-radius: 20px;
    -webkit-box-shadow: 0 0 0 10px #bbada0;
            box-shadow: 0 0 0 10px #bbada0;
    outline: 10px solid #bbada0;
    text-align: center;
}
@-webkit-keyframes newOne {
    0% {
       transform: rotate3D(0, 1, 0, 45deg);
       -webkit-transform: rotate3D(0, 1, 0, 45deg);
    }
    25% {
        transform: rotate3D(0, 1, 0, 135deg);
        -webkit-transform: rotate3D(0, 1, 0, 135deg);
    }
    50% {
        transform: rotate3D(0, 1, 0, 180deg);
        -webkit-transform: rotate3D(0, 1, 0, 180deg);
    }
    75% {
        transform: rotate3D(0, 1, 0, 315deg);
        -webkit-transform: rotate3D(0, 1, 0, 315deg);
    }
    100% {
        transform: rotate3D(0, 1, 0, 360deg);
        -webkit-transform: rotate3D(0, 1, 0, 360deg);
    }
}
@keyframes newOne {
    0% {
       transform: rotate3D(0, 1, 0, 45deg);
       -webkit-transform: rotate3D(0, 1, 0, 45deg);
    }
    25% {
        transform: rotate3D(0, 1, 0, 135deg);
        -webkit-transform: rotate3D(0, 1, 0, 135deg);
    }
    50% {
        transform: rotate3D(0, 1, 0, 180deg);
        -webkit-transform: rotate3D(0, 1, 0, 180deg);
    }
    75% {
        transform: rotate3D(0, 1, 0, 315deg);
        -webkit-transform: rotate3D(0, 1, 0, 315deg);
    }
    100% {
        transform: rotate3D(0, 1, 0, 360deg);
        -webkit-transform: rotate3D(0, 1, 0, 360deg);
    }
}
.animation{
    animation: newOne .8s ease-in-out;
    -webkit-animation: newOne .8s ease-in-out;
}
@-webkit-keyframes combineOne {
    0% {
        transform: scale(0.8);
        -webkit-transform: scale(0.8);
    }
    25% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
    75% {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
    }
    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
}
@keyframes combineOne {
    0% {
        transform: scale(0.8);
        -webkit-transform: scale(0.8);
    }
    25% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
    75% {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
    }
    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
}
@-webkit-keyframes combineTwo {
    0% {
        transform: scale(0.8);
        -webkit-transform: scale(0.8);
    }
    25% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
    75% {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
    }
    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
}
@keyframes combineTwo {
    0% {
        transform: scale(0.8);
        -webkit-transform: scale(0.8);
    }
    25% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
    75% {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
    }
    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
}

.combineAnimationtrue {
    animation: combineOne .8s ease-in-out;
    -webkit-animation: combineOne .8s ease-in-out;
}
.combineAnimationfalse {
    animation: combineTwo .8s ease-in-out;
    -webkit-animation: combineTwo .8s ease-in-out;
}
